.card {
  height: 245px;
  position: relative;
  text-align: center;
  border: none;
}

.card > .img-container {
  height: 95%;
  width: 95%;
  background:#6CADDC;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  overflow: hidden;
}
.card > .img-container > img {
  min-height: 100%;
  min-width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.card > .img-container:hover {
  -webkit-box-shadow: 0 3px 6px #777, 0 3px 6px #777;
          box-shadow: 0 3px 6px #777, 0 3px 6px #777;
}



